@import '../variables'

.mySwiper
    width: 100%
    padding: 1rem 0 4rem 0!important
.swiper-slide
    display: -webkit-box
    display: -ms-flexbox
    display: -webkit-flex
    display: flex
    flex-wrap: wrap
    justify-content: center
    gap: 1.5rem
    height: auto!important
    -webkit-box-pack: center
    -ms-flex-pack: center
    -webkit-justify-content: center
    -webkit-box-align: center
    -ms-flex-align: center
    -webkit-align-items: center
    align-items: center

.swiper-navigation-button, .swiper-pagination
    display: flex
    align-items: center
    position: absolute
    bottom: 0.5% !important

.swiper-navigation-button
    width: 6rem
    height: 2rem!important
    right: 3rem

.swiper-button-prev,
.swiper-button-next
    width: 1.7rem!important
    height: 1.7rem!important
    z-index: 2!important


.swiper-button-prev:after,
.swiper-button-next:after
    width: 1.7rem
    height: 1.7rem
    display: flex
    justify-content: center
    align-items: center
    border-radius: 1rem
    font-size: 1rem!important

.swiper-button-prev:after
    position: relative
    left: -10px
    color: #59DBF7
    padding: 0 2px 0 0
    background: white
    border: 1px solid #59DBF7

.swiper-button-next:after
    color: white
    padding: 0 0 0 2px
    background: #59DBF7
    z-index: 100!important


.swiper-pagination
    @include flexAlignment(center, flex-start, center)
    left: 3rem!important

    height: 2rem!important
    max-width: 60%

.swiper-pagination-bullet
    width: 20px!important
    height: 20px!important
    margin: 0 8px!important
    transition: 0.2s ease-in-out!important

.swiper-pagination-bullet-active
    background: #00D2FF!important
    width: 60px!important
    border-radius: 1rem!important

@media (max-width: $xxl)
  .swiper-pagination
      left: 0!important
  .swiper-navigation-button
      right: 0!important

@media (max-width: $md)
  .swiper-navigation-button
    display: none!important
  .swiper-pagination
      @include flexAlignment(center, center, center)
      max-width: 100% !important
  .swiper-pagination-bullet
    width: 16px!important
    height: 16px!important
    margin: 0 8px!important
  .swiper-pagination-bullet-active
    width: 60px!important

@media (max-width: $sm)
  .swiper-pagination-bullet
    width: 10px!important
    height: 10px!important
    margin: 0 6px!important
  .swiper-pagination-bullet-active
    width: 40px!important

@media (max-width: $xs)
  .swiper-pagination-bullet-active
    width: 10px!important
